export default "import React, { useState } from 'react';\nimport { useForm } from '@mantine/hooks';\nimport { EnvelopeClosedIcon, LockClosedIcon } from '@modulz/radix-icons';\nimport {\n  TextInput,\n  PasswordInput,\n  Group,\n  Checkbox,\n  Button,\n  Paper,\n  Text,\n  LoadingOverlay,\n  useMantineTheme,\n} from '../../src';\n\nexport interface AuthenticationFormProps {\n  noShadow?: boolean;\n  noPadding?: boolean;\n  noSubmit?: boolean;\n  style?: React.CSSProperties;\n}\n\nexport function AuthenticationForm({\n  noShadow,\n  noPadding,\n  noSubmit,\n  style,\n}: AuthenticationFormProps) {\n  const [formType, setFormType] = useState<'register' | 'login'>('register');\n  const [loading, setLoading] = useState(false);\n  const [error, setError] = useState<string>(null);\n  const theme = useMantineTheme();\n  const inputVariant = theme.colorScheme === 'dark' ? 'filled' : 'default';\n\n  const toggleFormType = () => {\n    setFormType((current) => (current === 'register' ? 'login' : 'register'));\n    setError(null);\n  };\n\n  const form = useForm({\n    initialValues: {\n      firstName: '',\n      lastName: '',\n      email: '',\n      password: '',\n      termsOfService: true,\n    },\n\n    validationRules: {\n      firstName: (value) => formType === 'login' || value.trim().length >= 2,\n      lastName: (value) => formType === 'login' || value.trim().length >= 2,\n      email: (value) => /^\\S+@\\S+$/.test(value),\n      password: (value) => /^(?=.*[A-Za-z])(?=.*\\d)[A-Za-z\\d]{6,}$/.test(value),\n    },\n  });\n\n  const handleSubmit = () => {\n    setLoading(true);\n    setError(null);\n    setTimeout(() => {\n      setLoading(false);\n      setError(\n        formType === 'register'\n          ? 'User with this email already exists'\n          : 'User with this email does not exist'\n      );\n    }, 3000);\n  };\n\n  return (\n    <Paper\n      padding={noPadding ? 0 : 'lg'}\n      shadow={noShadow ? null : 'sm'}\n      style={{\n        position: 'relative',\n        backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.white,\n        ...style,\n      }}\n    >\n      <form onSubmit={form.onSubmit(handleSubmit)}>\n        <LoadingOverlay visible={loading} />\n        {formType === 'register' && (\n          <div style={{ display: 'flex', marginBottom: 15 }}>\n            <TextInput\n              data-autofocus\n              required\n              placeholder=\"Your first name\"\n              label=\"First name\"\n              style={{ marginRight: 20, flex: '0 0 calc(50% - 10px)' }}\n              value={form.values.firstName}\n              onChange={(event) => form.setFieldValue('firstName', event.currentTarget.value)}\n              onFocus={() => form.setFieldError('firstName', false)}\n              error={form.errors.firstName && 'First name should include at least 2 characters'}\n              variant={inputVariant}\n            />\n\n            <TextInput\n              required\n              placeholder=\"Your last name\"\n              label=\"Last name\"\n              style={{ flex: '0 0 calc(50% - 10px)' }}\n              value={form.values.lastName}\n              onChange={(event) => form.setFieldValue('lastName', event.currentTarget.value)}\n              onFocus={() => form.setFieldError('lastName', false)}\n              error={form.errors.lastName && 'Last name should include at least 2 characters'}\n              variant={inputVariant}\n            />\n          </div>\n        )}\n\n        <TextInput\n          required\n          placeholder=\"Your email\"\n          label=\"Email\"\n          icon={<EnvelopeClosedIcon />}\n          value={form.values.email}\n          onChange={(event) => form.setFieldValue('email', event.currentTarget.value)}\n          onFocus={() => form.setFieldError('email', false)}\n          error={form.errors.email && 'Field should contain a valid email'}\n          variant={inputVariant}\n        />\n\n        <PasswordInput\n          style={{ marginTop: 15 }}\n          required\n          placeholder=\"Password\"\n          label=\"Password\"\n          showPasswordLabel=\"Show password\"\n          hidePasswordLabel=\"Hide password\"\n          icon={<LockClosedIcon />}\n          value={form.values.password}\n          onChange={(event) => form.setFieldValue('password', event.currentTarget.value)}\n          onFocus={() => form.setFieldError('password', false)}\n          variant={inputVariant}\n          error={\n            form.errors.password &&\n            'Password should contain 1 number, 1 letter and at least 6 characters'\n          }\n        />\n\n        {formType === 'register' && (\n          <Checkbox\n            style={{ marginTop: 20 }}\n            label=\"I agree to sell my soul and privacy to this corporation\"\n            checked={form.values.termsOfService}\n            onChange={(event) => form.setFieldValue('termsOfService', event.currentTarget.checked)}\n          />\n        )}\n\n        {error && (\n          <Text color=\"red\" size=\"sm\" style={{ marginTop: 10 }}>\n            {error}\n          </Text>\n        )}\n\n        {!noSubmit && (\n          <Group position=\"apart\" style={{ marginTop: 25 }}>\n            <Button variant=\"link\" color=\"gray\" onClick={toggleFormType} size=\"sm\">\n              {formType === 'register'\n                ? 'Have an account? Login'\n                : \"Don't have an account? Register\"}\n            </Button>\n\n            <Button color=\"blue\" type=\"submit\">\n              {formType === 'register' ? 'Register' : 'Login'}\n            </Button>\n          </Group>\n        )}\n      </form>\n    </Paper>\n  );\n}\n";