export default "import React, { useState, useRef } from 'react';\nimport { useId, useMergedRef, useClickOutside, useFocusTrap } from '@mantine/hooks';\nimport {\n  useMantineTheme,\n  InputWrapper,\n  Input,\n  InputProps,\n  InputWrapperBaseProps,\n  Transition,\n  Paper,\n  Group,\n  ColorSwatch,\n  Text,\n} from '@mantine/core';\n\ninterface ColorInputProps\n  extends InputProps,\n    InputWrapperBaseProps,\n    Omit<React.ComponentPropsWithoutRef<'button'>, 'value' | 'onChange'> {\n  value: string;\n  onChange(color: string): void;\n  data: string[];\n}\n\nexport function ColorInput({\n  id,\n  value,\n  placeholder,\n  onChange,\n  data,\n  required,\n  description,\n  label,\n  error,\n  ...others\n}: ColorInputProps) {\n  const theme = useMantineTheme();\n  const uuid = useId(id);\n  const controlRef = useRef<HTMLButtonElement>();\n  const [dropdownOpened, setDropdownOpened] = useState(false);\n  const focusTrapRef = useFocusTrap();\n  const closeDropdown = () => setDropdownOpened(false);\n  const clickOutsideRef = useClickOutside(closeDropdown);\n  const dropdownRef = useMergedRef(focusTrapRef, clickOutsideRef);\n\n  const handleChange = (color: string) => {\n    onChange(color);\n    closeDropdown();\n  };\n\n  const colors = data.map((color) => (\n    <ColorSwatch\n      style={{ cursor: 'pointer' }}\n      key={color}\n      component=\"button\"\n      color={color}\n      onClick={() => handleChange(color)}\n    />\n  ));\n\n  return (\n    <InputWrapper\n      required={required}\n      id={uuid}\n      label={label}\n      error={error}\n      description={description}\n    >\n      <div style={{ position: 'relative' }}>\n        <Input\n          component=\"button\"\n          id={uuid}\n          onClick={() => setDropdownOpened(true)}\n          styles={{ input: { cursor: 'pointer' } }}\n          elementRef={controlRef}\n          {...others}\n        >\n          {value ? (\n            <div style={{ display: 'flex', alignItems: 'center' }}>\n              <ColorSwatch color={value} size={16} style={{ marginRight: 10 }} />\n              <Text size=\"sm\" transform=\"uppercase\" style={{ lineHeight: 1 }}>\n                {value}\n              </Text>\n            </div>\n          ) : (\n            <Text color=\"gray\" size=\"sm\">\n              {placeholder}\n            </Text>\n          )}\n        </Input>\n\n        <Transition\n          transition=\"skew-up\"\n          duration={250}\n          mounted={dropdownOpened}\n          // Wait for focus trap to unmount and focus control\n          onExited={() => setTimeout(() => controlRef.current.focus(), 10)}\n        >\n          {(transitionStyles) => (\n            <Paper\n              shadow=\"md\"\n              padding=\"md\"\n              elementRef={dropdownRef}\n              style={{\n                ...transitionStyles,\n                position: 'absolute',\n                top: 0,\n                left: 0,\n                right: 0,\n                border: `1px solid ${\n                  theme.colorScheme === 'dark' ? theme.colors.dark[6] : theme.colors.gray[2]\n                }`,\n              }}\n              onKeyDownCapture={(event) => {\n                if (event.nativeEvent.code === 'Escape') {\n                  closeDropdown();\n                }\n              }}\n            >\n              <Group position=\"center\">{colors}</Group>\n            </Paper>\n          )}\n        </Transition>\n      </div>\n    </InputWrapper>\n  );\n}\n";